import { cloneDeep } from 'lodash-es';
import { SnapshotIn } from 'mobx-state-tree';

import { RootModel } from './Root.js';

/*
       .
*/
const MODELS_EXCLUDED_FROM_REHYDRATION: Array<keyof SnapshotIn<RootModel>> = ['settings', 'sonataState'];

export const excludeModelsFromRehydration = (
    store: RootModel,
    snapshot: SnapshotIn<RootModel>
): SnapshotIn<RootModel> => {
    return MODELS_EXCLUDED_FROM_REHYDRATION.reduce(
        (accumulator: SnapshotIn<RootModel>, modelName: keyof SnapshotIn<RootModel>) => {
            accumulator[modelName] = store[modelName];
            return accumulator;
        },
        cloneDeep(snapshot)
    );
};
