'use client';

import { ReactNode, use } from 'react';

import { TvmDestinations } from '@yandex-music-int/resource-collection';

import {
    WithChildren,
    useContainer,
    CONTAINER_TVM,
    CONTAINER_CONFIG_TOKEN,
    Tvm,
    ensureContainerExists
} from '@/shared/lib/index.js';

interface Props extends WithChildren {}

/*
         TVM ,
                
*/
export const TvmProvider = ({ children }: Props): ReactNode => {
    if (typeof window === 'undefined') {
        // eslint-disable-next-line react-hooks/rules-of-hooks
        const container = useContainer();
        const config = container.get(CONTAINER_CONFIG_TOKEN);

        if (config.tvm.enabled) {
            const tvm = container.get(CONTAINER_TVM);

            if (ensureContainerExists(tvm, Tvm<TvmDestinations>)) {
                use(tvm.loadTvmTickets());
            }
        }
    }

    return children;
};
