/* eslint-disable react-hooks/rules-of-hooks */

'use client';

import { clsx } from 'clsx';
import { useEffect, ReactNode } from 'react';
import { useIntl } from 'react-intl';

import { Notification } from '@yandex-music-int/sol/components/Notification';

import { sendApplicationReadyEvent, handleDesktopEvents } from '@/features/desktop-bridge/index.js';

import { useNotify, getPlatform, NotificationContainerId, WithChildren, Platform } from '@/shared/lib/index.js';

interface Props extends WithChildren {}

export const DesktopProvider = ({ children }: Props): ReactNode => {
    const platform = getPlatform();

    if (process.env.BUILD_TYPE === 'desktop') {
        const { formatMessage } = useIntl();
        const { notify } = useNotify();

        useEffect(() => {
            sendApplicationReadyEvent();
        }, []);

        useEffect(() => {
            handleDesktopEvents(formatMessage, notify);
        }, [notify, formatMessage]);
    }

    return (
        <>
            {children}
            {process.env.BUILD_TYPE === 'desktop' && (
                <Notification
                    className={clsx({
                        updateNotificationContainer: true,
                        updateNotificationContainer_application_windows: platform === Platform.WINDOWS,
                        updateNotificationContainer_application_macos: platform === Platform.MACOS
                    })}
                    enableMultiContainer
                    containerId={NotificationContainerId.APPLICATION_UPDATE}
                    position="top-center"
                />
            )}
        </>
    );
};
