import { types, Instance, flow, getEnv, cast } from 'mobx-state-tree';

import {
    UsersResourceCreatedPlaylists,
    UsersResourceCreatedPlaylistsParams,
    Playlist as PlaylistApi
} from '@yandex-music-int/resource-collection';

import { Playlist, playlistResponseToModel } from '@/entities/playlist/index.js';

import { LoadingState, StoreEnv } from '@/shared/lib/index.js';
import { Sort, Pager } from '@/shared/models/index.js';

import { updateItemsByPage } from '../lib/updateItemsByPage.js';

export const PlaylistsCreated = types
    .model('PlaylistsCreated', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        sort: types.maybeNull(Sort),
        items: types.array(types.maybeNull(Playlist)),
        pager: types.maybeNull(Pager),
        alreadyRequestedPages: types.map(types.number),
        kinds: types.array(types.number),
        requests: types.maybeNull(types.number)
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getKinds: flow(function* (userId: number): Generator<Promise<number[]>, void, number[]> {
                let playlistsKinds: number[] = [];
                const { usersResource } = getEnv<StoreEnv>(self);

                playlistsKinds = yield usersResource.getPlaylistsKinds({
                    userId
                });

                self.kinds = cast(playlistsKinds);
            })
        };
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getData: flow(function* ({
                userId,
                page = 0,
                pageSize = 20,
                withLikesCount
            }: UsersResourceCreatedPlaylistsParams): Generator<
                Promise<UsersResourceCreatedPlaylists | void>,
                void,
                UsersResourceCreatedPlaylists
            > {
                const { usersResource, logger } = getEnv<StoreEnv>(self);

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                if (self.alreadyRequestedPages.has(`${page}`)) {
                    return;
                }

                if (typeof page === 'number') {
                    self.alreadyRequestedPages.set(`${page}`, page);
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    if (self.kinds.length === 0) {
                        yield self.getKinds(userId);
                    }

                    const initialItem = page * pageSize;
                    const finalItem = initialItem + pageSize;

                    const playlistsKinds = self.kinds.slice(initialItem, finalItem);

                    const playlists = yield usersResource.getPlaylistsByKinds({
                        userId,
                        kinds: playlistsKinds,
                        withLikesCount,
                        withTracks: false
                    });

                    self.requests = (self.requests ?? 0) + 1;

                    const pager = {
                        page,
                        perPage: pageSize,
                        total: self.kinds.length
                    };

                    if (self.items.length === 0) {
                        self.items = cast(Array.from({ length: pager.total }, () => null));
                    }

                    const mappedRawItems = playlists.map((playlist: PlaylistApi) => playlistResponseToModel(playlist));

                    updateItemsByPage({
                        items: self.items,
                        mappedRawItems,
                        page,
                        pageSize
                    });

                    self.pager = pager;

                    self.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    self.loadingState = LoadingState.REJECT;
                }
            })
        };
    });

export type PlaylistsCreatedModel = Instance<typeof PlaylistsCreated>;
