import { Instance, cast, flow, getEnv, types } from 'mobx-state-tree';

import { UsersResourceLikedArtists, UsersResourceLikedArtistsParams } from '@yandex-music-int/resource-collection';

import { Artist, artistResponseToModel } from '@/entities/artist/index.js';

import { LoadingState, StoreEnv } from '@/shared/lib/index.js';
import { Sort } from '@/shared/models/index.js';

export const Artists = types
    .model('Artists', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        sort: types.maybeNull(Sort),
        items: types.array(Artist)
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getData: flow(function* ({
                userId,
                page,
                pageSize,
                sortBy,
                sortOrder
            }: UsersResourceLikedArtistsParams): Generator<
                Promise<UsersResourceLikedArtists>,
                void,
                UsersResourceLikedArtists
            > {
                const { usersResource, logger } = getEnv<StoreEnv>(self);

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                self.sort = null;

                if (sortBy && sortOrder) {
                    self.sort = {
                        sortBy,
                        sortOrder
                    };
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const data = yield usersResource.getLikedArtists({
                        userId,
                        page,
                        pageSize,
                        sortBy,
                        sortOrder
                    });

                    self.items = cast(data.map(artistResponseToModel));

                    self.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    self.loadingState = LoadingState.REJECT;
                }
            })
        };
    });

export type ArtistsModel = Instance<typeof Artists>;
