import { types, Instance, flow, getEnv, cast } from 'mobx-state-tree';

import { UsersResourceLikedAlbums, UsersResourceLikedAlbumsParams, Album } from '@yandex-music-int/resource-collection';

import { AlbumWithArtists, albumWithArtistsResponseToModel } from '@/features/album/index.js';

import { LoadingState, StoreEnv } from '@/shared/lib/index.js';
import { Sort } from '@/shared/models/index.js';

export const Albums = types
    .model('Albums', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        sort: types.maybeNull(Sort),
        items: types.array(AlbumWithArtists)
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getData: flow(function* ({
                userId,
                page,
                pageSize,
                sortBy,
                sortOrder,
                metaType
            }: UsersResourceLikedAlbumsParams): Generator<
                Promise<UsersResourceLikedAlbums>,
                void,
                UsersResourceLikedAlbums
            > {
                const { usersResource, logger } = getEnv<StoreEnv>(self);

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                self.sort = null;

                if (sortBy && sortOrder) {
                    self.sort = {
                        sortBy,
                        sortOrder
                    };
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const data = yield usersResource.getLikedAlbums({
                        userId,
                        page,
                        pageSize,
                        sortBy,
                        sortOrder,
                        metaType
                    });

                    self.items = cast(
                        data.map(({ album }: { album: Album }) => albumWithArtistsResponseToModel(album))
                    );

                    self.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    self.loadingState = LoadingState.REJECT;
                }
            })
        };
    });

export type AlbumsModel = Instance<typeof Albums>;
