'use client';

import { useRouter } from 'next/navigation.js';
import { ReactElement, useCallback, useMemo, useState } from 'react';

import { HistoryNode } from '@/shared/lib/history/HistoryNode.js';
import { HistoryNodeData } from '@/shared/lib/history/types/HistoryNodeData.js';
import {
    HistoryList,
    HistoryContext,
    WithChildren,
    useContainer,
    CONTAINER_SESSION_STORAGE
} from '@/shared/lib/index.js';

interface Props extends WithChildren {}

let singletonHistory: HistoryList | null = null;

export const HistoryProvider = ({ children }: Props): ReactElement => {
    const container = useContainer();
    const router = useRouter();

    const [canBack, setCanBack] = useState(false);
    const [canForward, setCanForward] = useState(false);
    const [state, setState] = useState<HistoryNode | null>(null);

    const history = useMemo((): HistoryList | null => {
        if (typeof window === 'undefined') {
            return null;
        }

        if (singletonHistory) {
            return singletonHistory;
        }

        const sessionStorage = container.get(CONTAINER_SESSION_STORAGE);

        singletonHistory = new HistoryList(sessionStorage);

        return singletonHistory;
    }, [container]);

    const updateState = useCallback(() => {
        if (!history) {
            return;
        }

        setCanBack(history.canBack);
        setCanForward(history.canForward);
        setState(history.state);
    }, [history]);

    const replaceState = useCallback(
        (data: HistoryNodeData) => {
            if (!history) {
                return;
            }

            history.replaceState(data);
            router.replace(data.href);
        },
        [history, router]
    );

    const pushState = useCallback(
        (data: HistoryNodeData) => {
            if (!history) {
                return;
            }

            history.pushState(data);
            updateState();
        },
        [history, updateState]
    );

    const back = useCallback(() => {
        if (!history) {
            return;
        }

        history.back();
        updateState();
        router.back();
    }, [history, router, updateState]);

    const forward = useCallback(() => {
        if (!history) {
            return;
        }

        history.forward();
        updateState();
        router.forward();
    }, [history, router, updateState]);

    return (
        <HistoryContext.Provider
            value={{
                pushState,
                replaceState,
                canForward,
                canBack,
                back,
                forward,
                state
            }}
        >
            {children}
        </HistoryContext.Provider>
    );
};
