import { getPlatform } from '../lib/platform/getPlatform.js';
import { getPlatformName } from '../lib/platform/getPlatformName.js';

import { EXTERNAL_API_CLIENT_VERSION } from './constants/externalApiClientVersion.js';
import { ClientType } from './types/ClientType.js';

const createClientName = (type: string, version: string): string => {
    return `${type}/${version}`;
};

export const getExternalApiClientName = (): string => {
    if (process.env.BUILD_TYPE === 'desktop') {
        if (typeof window !== 'undefined') {
            const clientType = `${ClientType.DESKTOP}${getPlatformName(getPlatform())}`;

            return createClientName(clientType, EXTERNAL_API_CLIENT_VERSION);
        }
    }

    return createClientName(ClientType.WEB, EXTERNAL_API_CLIENT_VERSION);
};
