import { IMSTMap, ISimpleType } from 'mobx-state-tree';

import { Track } from '@yandex-music-int/resource-collection';

import { trackWithArtistsResponseToModel } from '@/features/track/index.js';

import { LoadingState } from '@/shared/lib/index.js';

import { VolumeItemModel } from '../model/VolumeItem.js';

import { createVolumeItemTrack, createVolumeItemText } from './createVolumeItem.js';
import { getVolumeItemKeyText, getVolumeItemKeyTrack } from './getVolumeItemKey.js';

/**
 *        
 * @param data      
 * @param items      
 * @param indexItems Map,   -  id ,      items
 */
export const insertDataToVolumeItems = (
    data: Track[],
    items: VolumeItemModel[],
    indexItems: IMSTMap<ISimpleType<number>>
): void => {
    data.forEach((track: Track) => {
        if (track.albums?.[0]) {
            const trackIndex = indexItems.get(getVolumeItemKeyTrack(track.id));
            if (typeof trackIndex !== 'undefined') {
                items[trackIndex] = createVolumeItemTrack(
                    track.id,
                    LoadingState.RESOLVE,
                    trackWithArtistsResponseToModel(track),
                    track.albums[0]?.trackPosition?.index
                );
            }
            const volumeIndex = indexItems.get(getVolumeItemKeyText(track.id));
            if (typeof volumeIndex !== 'undefined') {
                items[volumeIndex] = createVolumeItemText(track.albums[0]?.trackPosition?.volume);
            }
        }
    });
};
