import { types, Instance } from 'mobx-state-tree';

import { Landing } from '@/widgets/landing/index.js';

import { Albums } from './Albums.js';
import { Artists } from './Artists.js';
import { PlaylistsCreated } from './PlaylistsCreated.js';
import { PlaylistsLiked } from './PlaylistsLiked.js';

export const Collection = types.model('Collection', {
    landing: Landing,
    albums: Albums,
    playlistsLiked: PlaylistsLiked,
    playlistsCreated: PlaylistsCreated,
    artists: Artists
});

export type CollectionModel = Instance<typeof Collection>;
