import { ReactNode } from 'react';
import { IntlFormatters } from 'react-intl';

import { NotificationContainerId, NotifyFunction, DesktopEvents } from '@/shared/lib/index.js';

import { NotificationUpdate } from '../ui/NotificationUpdate/NotificationUpdate.js';

export const handleUpdate = (
    formatMessage: IntlFormatters<ReactNode>['formatMessage'],
    notify: NotifyFunction
): void => {
    let latestVersion: string | null = null;

    window.desktopEvents?.on(DesktopEvents.ON_UPDATE_AVAILABLE, (event: unknown, version: string): void => {
        if (latestVersion === version) {
            return;
        }

        latestVersion = version;

        notify(<NotificationUpdate formatMessage={formatMessage} version={version} />, {
            containerId: NotificationContainerId.APPLICATION_UPDATE
        });
    });
};
