import { IMSTMap, ISimpleType } from 'mobx-state-tree';

import { TrackId } from '@yandex-music-int/resource-collection';

import { LoadingState } from '@/shared/lib/index.js';

import { VolumeItemModel } from '../model/VolumeItem.js';

import { createVolumeItemTrack } from './createVolumeItem.js';

/**
 *       
 * @param trackIds  id ,     
 * @param items      
 * @param indexItems Map,   -  id ,      items
 * @param state     
 */
export const updateVolumeItemsState = (
    trackIds: TrackId[],
    items: VolumeItemModel[],
    indexItems: IMSTMap<ISimpleType<number>>,
    state: LoadingState
): void => {
    trackIds.forEach((id: TrackId) => {
        const trackIndex = indexItems.get(String(id));
        if (typeof trackIndex !== 'undefined') {
            items[trackIndex] = createVolumeItemTrack(id, state);
        }
    });
};
