'use client';

import { ReactNode, use } from 'react';

import {
    Blackbox,
    WithChildren,
    useContainer,
    CONTAINER_BLACKBOX,
    CONTAINER_CONFIG_TOKEN,
    ensureContainerExists
} from '@/shared/lib/index.js';

interface Props extends WithChildren {
    host: string;
    userIp: string;
}

export const BlackboxProvider = ({ children, host, userIp }: Props): ReactNode => {
    if (typeof window === 'undefined') {
        // eslint-disable-next-line react-hooks/rules-of-hooks
        const container = useContainer();
        const config = container.get(CONTAINER_CONFIG_TOKEN);
        const { blackbox: blackboxConfig } = config;

        if (blackboxConfig.enabled) {
            const hostname = host.replace(/:[\d]+$/i, '');
            const blackbox = container.get(CONTAINER_BLACKBOX);

            if (ensureContainerExists(blackbox, Blackbox)) {
                use(blackbox.loadSession(hostname, userIp));
            }
        }
    }

    return children;
};
