import { DesktopEvents } from '@/shared/lib/index.js';

export const sendWindowMinimizeEvent = (): void => {
    window.desktopEvents?.send(DesktopEvents.WINDOW_MINIMIZE);
};

export const sendWindowMaximizeEvent = (): void => {
    window.desktopEvents?.send(DesktopEvents.WINDOW_MAXIMIZE);
};

export const sendWindowCloseEvent = (): void => {
    window.desktopEvents?.send(DesktopEvents.WINDOW_CLOSE);
};
