import { TrackId } from '@yandex-music-int/resource-collection';

import { LoadingState } from '@/shared/lib/index.js';

import { PlaylistItemModel } from '../model/PlaylistItem.js';

/**
 *       
 * @param trackIds  id ,     
 * @param items     
 * @param state     
 */
export const updatePlaylistItemsState = (
    trackIds: TrackId[],
    items: PlaylistItemModel[],
    state: LoadingState
): void => {
    items.forEach((item: PlaylistItemModel) => {
        if (trackIds.includes(item.id)) {
            item = {
                id: item.id,
                data: null,
                loadingState: state
            };
        }
    });
};
