import { TrackId } from '@yandex-music-int/resource-collection';

/**
 *    id - {trackId}:{albumId}
 * @param trackIds  id 
 * @param albumId Id 
 * @returns   id
 */
export const getPreparedIds = (trackIds: TrackId[], albumId: number): string[] => {
    return trackIds.map((trackId: TrackId) => {
        return `${trackId}:${albumId}`;
    });
};
