interface UpdateItemsByPage<T> {
    items: unknown[];
    mappedRawItems: T[];
    page: number;
    pageSize: number;
}

export function updateItemsByPage<T>({ items, mappedRawItems, page, pageSize }: UpdateItemsByPage<T>): void {
    const startIndex = page * pageSize;

    let localIndex = 0;

    for (let index = startIndex; index < startIndex + pageSize; index++) {
        if (mappedRawItems[localIndex]) {
            items[index] = mappedRawItems[localIndex]!;
        }

        localIndex++;
    }
}
