import { IMSTMap, ISimpleType } from 'mobx-state-tree';

import { AlbumsResourceWithTracksIdsResponse, TrackId } from '@yandex-music-int/resource-collection';

import { LoadingState } from '@/shared/lib/index.js';

import { VolumeItemModel } from '../model/VolumeItem.js';

import { createVolumeItemText, createVolumeItemTrack } from './createVolumeItem.js';
import { getVolumeItemKeyText, getVolumeItemKeyTrack } from './getVolumeItemKey.js';

const COUNT_VOLUMES_WITHOUT_TEXT = 1;

/**
 *      volumes: [[{id}, {id}], [...]]   
 *     1 ,    ,     
 * @param album    volumes,      
 * @param items      
 * @param indexItems Map,   -  id ,      items
 * @param trackIndexInContext Map,   -  id ,      (  )
 */
export const makeFlatVolumeItems = (
    album: AlbumsResourceWithTracksIdsResponse,
    items: VolumeItemModel[],
    indexItems: IMSTMap<ISimpleType<number>>,
    trackIndexInContext: IMSTMap<ISimpleType<number>>
): void => {
    let trackIndex = 0;
    album.volumes.forEach((volume: Array<Record<'id', TrackId>>) => {
        if (album.volumes.length > COUNT_VOLUMES_WITHOUT_TEXT && volume[0]) {
            items.push(createVolumeItemText());
            indexItems.set(getVolumeItemKeyText(volume[0].id), items.length - 1);
        }
        volume.forEach((trackId: Record<'id', TrackId>) => {
            items.push(createVolumeItemTrack(trackId.id, LoadingState.IDLE));
            indexItems.set(getVolumeItemKeyTrack(trackId.id), items.length - 1);
            trackIndexInContext.set(getVolumeItemKeyTrack(trackId.id), trackIndex);
            trackIndex++;
        });
    });
};
