import { ReactNode } from 'react';
import { IntlFormatters } from 'react-intl';

import { NotifyFunction } from '@/shared/lib/index.js';

import { handleUpdate } from './handleUpdate.js';

export const handleDesktopEvents = (
    formatMessage: IntlFormatters<ReactNode>['formatMessage'],
    notify: NotifyFunction
): void => {
    handleUpdate(formatMessage, notify);
};
