import { SearchFilter, SearchType } from '@yandex-music-int/resource-collection';

import { SUPPORTED_TYPES } from './constants/SupportedTypes.js';

export const getSupportedTypes = (filter: SearchFilter | undefined): SearchType[] => {
    /*
           ugc    
    */
    if (filter === SearchFilter.TOP) {
        return SUPPORTED_TYPES.filter((type: SearchType) => type !== SearchType.UGC_TRACK);
    }

    return SUPPORTED_TYPES;
};
