'use client';

import { ReactElement, use } from 'react';

import { WithChildren, ContainerContext, getContainer } from '@/shared/lib/index.js';

interface Props extends WithChildren {
    hostTld: string;
    env: typeof process.env.NEXT_PUBLIC_CONFIG_ENV;
}

export const ContainerProvider = ({ children, hostTld, env }: Props): ReactElement => {
    const container = use(getContainer(hostTld, env));

    return <ContainerContext.Provider value={container}>{children}</ContainerContext.Provider>;
};
