'use client';

import { clsx } from 'clsx';
import { ReactElement, ReactNode, useCallback, useMemo } from 'react';
import { IntlFormatters } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { DesktopEvents } from '@/shared/lib/index.js';
import { BaseNotification } from '@/shared/ui/index.js';

import styles from './NotificationUpdate.module.scss';

interface Props {
    version: string;
    formatMessage: IntlFormatters<ReactNode>['formatMessage'];
    closeToast?: () => void;
}

export const NotificationUpdate = ({ version, formatMessage, closeToast }: Props): ReactElement => {
    const handleUpdate = useCallback((): void => {
        window.desktopEvents?.send(DesktopEvents.ON_UPDATE_INSTALL);
        closeToast?.();
    }, [closeToast]);

    const message = useMemo(() => {
        return (
            <div className={styles.message}>
                <Caption className={styles.text} variant="div" type="controls" size="m">
                    {formatMessage({ id: 'desktop.on-update-available' }, { version })}
                </Caption>
                <Button
                    className={styles.button}
                    onClick={handleUpdate}
                    variant="default"
                    color="secondary"
                    size="xs"
                    radius="xxxl"
                >
                    <Caption variant="div" type="controls" size="m">
                        {formatMessage({ id: 'desktop.update' })}
                    </Caption>
                </Button>
            </div>
        );
    }, [formatMessage, handleUpdate, version]);

    return <BaseNotification className={clsx(styles.root, styles.important)} message={message} />;
};
