'use client';

import { observer } from 'mobx-react-lite';
import { ReactElement, useEffect, useMemo } from 'react';

import { AudioCoreHtml5 } from '@yandex-music-int/audio-core-html5';
import { YaMusicContextData, YaMusicContextFactory, YaMusicContext } from '@yandex-music-int/player-contexts';
import { YaMusicEntityData, YaMusicEntityFactory, YaMusicEntity } from '@yandex-music-int/player-entities';
import {
    ApplyContextPlugin,
    AvailableActionsPlugin,
    PlaysPlugin,
    TotalPlayedTimePlugin,
    MediaControlsPlugin,
    LoggerPlugin
} from '@yandex-music-int/player-plugins';
import { DownloadInfoMediaProvider, YaMusicEntityProvider } from '@yandex-music-int/player-providers';
import { SonataPlayer } from '@yandex-music-int/sonata';

import { getExternalApiClientName } from '@/shared/api/index.js';
import {
    CONTAINER_AUTHORIZATION,
    CONTAINER_CONFIG_TOKEN,
    CONTAINER_HTTP_CLIENT_TOKEN,
    getTLDHost,
    SonataContext,
    StorageKeys,
    useContainer,
    useStore,
    WithChildren,
    UserAgent,
    CONTAINER_STORAGE,
    useLogger
} from '@/shared/lib/index.js';

interface Props extends WithChildren {
    hostTld: string;
    userAgent: UserAgent;
}

let singletonSonata: SonataPlayer<YaMusicContextData, YaMusicEntityData, YaMusicContext, YaMusicEntity> | null = null;

export const SonataProvider = observer(({ children, hostTld, userAgent }: Props): ReactElement => {
    const container = useContainer();
    const { sonataState } = useStore();
    const logger = useLogger();
    const storage = container.get(CONTAINER_STORAGE);

    const sonata = useMemo(() => {
        if (typeof window === 'undefined') {
            return null;
        }

        if (singletonSonata) {
            return singletonSonata;
        }

        const config = container.get(CONTAINER_CONFIG_TOKEN);
        const httpClientFactory = container.get(CONTAINER_HTTP_CLIENT_TOKEN);
        const httpClient = httpClientFactory({ credentials: 'include' });
        const authorization = container.get(CONTAINER_AUTHORIZATION);

        const contextFactory = new YaMusicContextFactory({
            httpClient,
            logger,
            config: {
                params: {
                    common: {
                        client: getExternalApiClientName(),
                        language: config.language,
                        oauth: authorization.token
                    }
                },
                prefixUrl: getTLDHost(config.player.prefixUrl, hostTld)
            }
        });

        const entityFactory = new YaMusicEntityFactory();

        const mediaProvider = new DownloadInfoMediaProvider({
            httpClient,
            config: {
                params: {
                    common: {
                        client: getExternalApiClientName(),
                        language: config.language,
                        oauth: authorization.token
                    }
                },
                prefixUrl: getTLDHost(config.player.prefixUrl, hostTld),
                secretKey: config.player.secretKey,
                overembed: config.player.overembed,
                externalDomain: config.player.externalDomain
            }
        });

        const entityProvider = new YaMusicEntityProvider({
            httpClient,
            config: {
                params: {
                    common: {
                        client: getExternalApiClientName(),
                        language: config.language,
                        oauth: authorization.token
                    }
                },
                prefixUrl: getTLDHost(config.player.prefixUrl, hostTld)
            }
        });

        const audioCore = new AudioCoreHtml5();

        singletonSonata = new SonataPlayer<YaMusicContextData, YaMusicEntityData, YaMusicContext, YaMusicEntity>({
            contextFactory,
            entityFactory,
            audioCore,
            mediaProvider,
            entityProvider,
            windowPaginationConfig: {
                windowSize: 30,
                setupQueueWindowSize: 80
            },
            plugins: [
                new AvailableActionsPlugin(),
                new ApplyContextPlugin(),
                new TotalPlayedTimePlugin(),
                new PlaysPlugin({
                    httpClient,
                    playsResourceConfig: {
                        params: {
                            common: {
                                client: getExternalApiClientName(),
                                language: config.language,
                                oauth: authorization.token
                            }
                        },
                        prefixUrl: getTLDHost(config.player.prefixUrl, hostTld)
                    }
                }),
                new MediaControlsPlugin({
                    browserName: userAgent.browserName,
                    browserVersion: userAgent.browserVersion
                }),
                new LoggerPlugin({ logger })
            ]
        });

        return singletonSonata;
    }, [container, hostTld, userAgent, logger]);

    useEffect(() => {
        const unsubscribeFromStatusChange = sonata?.state.playerState.status.onChange(() => {
            const currentEntity = sonata.state.queueState.currentEntity.value;
            const currentContextType = currentEntity?.context.data.type;
            const currentContextId = currentEntity?.context.data.meta.id;
            sonataState.setContextId(currentContextId);
            sonataState.setContextType(currentContextType);
        });

        const unsubscribeFromEventChange = sonata?.state.playerState.event.onChange(() => {
            const currentEntity = sonata.state.queueState.currentEntity.value;
            sonataState.setEntityMeta(currentEntity?.entity.data.meta ?? null);
            sonataState.setStatus(sonata.state.playerState.status.value);
        });

        let unsubscribeFromMoveBackwardChange: (() => void) | undefined;
        let unsubscribeFromMoveForwardChange: (() => void) | undefined;

        const unsubscribeFromContextChange = sonata?.state.currentContext.onChange(() => {
            unsubscribeFromMoveBackwardChange?.();
            unsubscribeFromMoveForwardChange?.();

            unsubscribeFromMoveBackwardChange =
                sonata?.state.currentContext.value?.availableActions.moveBackward.onChange(() => {
                    const canMoveBackward =
                        sonata?.state.currentContext.value?.availableActions.moveBackward.value ?? false;
                    sonataState.setCanMoveBackward(canMoveBackward);
                });

            unsubscribeFromMoveForwardChange =
                sonata?.state.currentContext.value?.availableActions.moveForward.onChange(() => {
                    const canMoveForward =
                        sonata?.state.currentContext.value?.availableActions.moveForward.value ?? false;
                    sonataState.setCanMoveForward(canMoveForward);
                });
        });

        const unsubscribeFromVolumeChange = sonata?.state.playerState.volume.onChange(() => {
            const newVolume = sonata.state.playerState.volume.value;

            if (typeof newVolume === 'number') {
                sonataState.setVolume(newVolume);
            }
        });

        return () => {
            unsubscribeFromEventChange?.();
            unsubscribeFromStatusChange?.();
            unsubscribeFromContextChange?.();
            unsubscribeFromVolumeChange?.();
        };
    }, [sonata, sonataState]);

    useEffect(() => {
        const volume: number = storage.get(StorageKeys.YmPlayerVolume);

        if (typeof volume === 'number') {
            sonata?.setVolume(volume);
            sonataState.setVolume(volume);
        } else {
            sonata?.setVolume(1);
            sonataState.setVolume(1);
        }
    }, [storage, sonata, sonataState]);

    return <SonataContext.Provider value={sonata}>{children}</SonataContext.Provider>;
});
