'use client';

import { ReactNode, useMemo } from 'react';
import { IntlProvider } from 'react-intl';

import { WithChildren, CompiledTranslations } from '@/shared/lib/index.js';

interface Props extends WithChildren {
    dictionary: CompiledTranslations;
}

export const TranslationsProvider = ({ dictionary, children }: Props): ReactNode => {
    const messages = useMemo(() => {
        return dictionary;
        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        /**
         * TODO:  "locale"
         *       MFRONT-5860,
         *   MFRONT-5433  MFRONT-5434
         */
        <IntlProvider defaultLocale="ru-RU" locale="ru-RU" messages={messages}>
            {children}
        </IntlProvider>
    );
};
