'use client';

import { throttle } from 'lodash-es';
import { observer } from 'mobx-react-lite';
import { ReactElement, useEffect, useMemo, useLayoutEffect, useState, useCallback } from 'react';

import {
    getLayoutName,
    getPlatform,
    useStore,
    userAgentLayout,
    Theme,
    LayoutContext,
    WithChildren,
    UserAgent,
    ColorScheme
} from '@/shared/lib/index.js';
import { SettingsModel } from '@/shared/models/Settings.js';

const THROTTLE_WAIT = 100;

interface Props extends WithChildren {
    userAgent: UserAgent;
    clientTld: string;
}

export const setClientSideLayout = (settings: SettingsModel): void => {
    if (typeof window !== 'undefined') {
        document.body.classList.remove('desktop');
        settings.setLayout(getLayoutName(window.innerWidth));
    }
};

export const LayoutProvider = observer(({ children, userAgent, clientTld }: Props): ReactElement<Props> => {
    const { settings } = useStore();
    const [contentRef, setContentRef] = useState<HTMLElement | null>(null);
    const platform = getPlatform();

    settings.setClientTld(clientTld);

    if (platform) {
        settings.setPlatform(platform);
    }

    if (!settings.layout && typeof window === 'undefined') {
        settings.setLayout(userAgentLayout(userAgent));
    }

    const theme = Theme.Dark;
    const colorScheme: ColorScheme = 'dark';

    const handleWindowResize = useMemo(() => {
        return throttle(() => {
            setClientSideLayout(settings);
        }, THROTTLE_WAIT);
    }, [settings]);

    useLayoutEffect(() => {
        setClientSideLayout(settings);
    }, [settings]);

    useEffect(() => {
        window.addEventListener('resize', handleWindowResize);

        return () => {
            window.removeEventListener('resize', handleWindowResize);
        };
    }, [handleWindowResize]);

    const setContentRefHandler = useCallback((ref: HTMLElement | null) => {
        setContentRef(ref);
    }, []);

    const value = useMemo(() => {
        return {
            theme,
            colorScheme,
            // eslint-disable-next-line @typescript-eslint/no-empty-function
            setTheme: () => {},
            contentRef,
            setContentRef: setContentRefHandler
        };
    }, [contentRef, setContentRefHandler, theme]);

    return <LayoutContext.Provider value={value}>{children}</LayoutContext.Provider>;
});
