import { TrackId } from '@yandex-music-int/resource-collection';

import { TrackWithArtistsModel } from '@/features/track/index.js';

import { LoadingState } from '@/shared/lib/index.js';

import { VolumeItemTextModel, VolumeItemTrackModel } from '../model/VolumeItem.js';

import { VolumeType } from './types/VolumeType.js';

export const createVolumeItemText = (data?: number): VolumeItemTextModel => {
    return {
        type: VolumeType.TEXT,
        data: data || null
    };
};

export const createVolumeItemTrack = (
    id: TrackId,
    loadingState: LoadingState,
    data?: TrackWithArtistsModel,
    position?: number
): VolumeItemTrackModel => {
    return {
        type: VolumeType.TRACK,
        id,
        data: data || null,
        loadingState,
        position
    };
};
