import { types, flow, getEnv, cast } from 'mobx-state-tree';

import {
    SearchResourceInstantMixedParams,
    SearchResourceInstantMixedResponse,
    SearchResourceFeedbackParams,
    SearchResourceFeedbackResponse,
    SearchFilter
} from '@yandex-music-int/resource-collection';

import { SearchItem } from '@/widgets/search/index.js';

import { LoadingState, StoreEnv } from '@/shared/lib/index.js';

import { getSupportedTypes } from '../lib/getSupportedTypes.js';
import { searchInstantMixedResultsResponseToModel } from '../lib/searchInstantMixedResultsResponseToModel.js';

const PAGE_SIZE = 36;

export const Search = types
    .model('Search', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        searchRequestId: types.optional(types.string, ''),
        results: types.array(SearchItem)
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getSearchResults: flow(function* ({
                text,
                filter
            }: SearchResourceInstantMixedParams): Generator<
                Promise<SearchResourceInstantMixedResponse | unknown>,
                void,
                SearchResourceInstantMixedResponse
            > {
                const { searchResource } = getEnv<StoreEnv>(self);

                try {
                    self.loadingState = LoadingState.PENDING;

                    const data = yield searchResource.getInstantMixedSearch({
                        text,
                        type: getSupportedTypes(filter),
                        //      ,       
                        filter: filter === SearchFilter.TOP ? undefined : filter,
                        withLikesCount: true,
                        page: 0,
                        //      36 
                        pageSize: PAGE_SIZE
                    });

                    if (data.results && data.results.length > 0) {
                        self.results = cast(searchInstantMixedResultsResponseToModel(data.results));
                        self.searchRequestId = data.searchRequestId;
                    }

                    self.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    self.loadingState = LoadingState.REJECT;
                }
            }),
            resetResults() {
                self.results = cast([]);
            },
            sendFeedback: flow(function* (
                params: SearchResourceFeedbackParams
            ): Generator<Promise<SearchResourceFeedbackResponse | unknown>, void, SearchResourceFeedbackResponse> {
                const { searchResource, logger } = getEnv<StoreEnv>(self);

                try {
                    yield searchResource.sendFeedback(params);
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }
                }
            })
        };
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => {
        return {
            get isLoading(): boolean {
                return self.loadingState === LoadingState.IDLE || self.loadingState === LoadingState.PENDING;
            },
            get isEmpty(): boolean {
                return (
                    (self.loadingState === LoadingState.RESOLVE || self.loadingState === LoadingState.REJECT) &&
                    self.results.length === 0
                );
            }
        };
    });
