'use client';

import { observer } from 'mobx-react-lite';
import { ReactNode } from 'react';

import {
    WithChildren,
    useUrl,
    useContainer,
    CONTAINER_AUTHORIZATION,
    getTldFromHost,
    useStore
} from '@/shared/lib/index.js';

interface Props extends WithChildren {
    host: string;
}

/*
          Authorization    
      ( User,  URL)   
*/
export const AuthorizationProvider = observer(({ children, host: serverSideHost }: Props): ReactNode => {
    const container = useContainer();
    const { user } = useStore();
    const authorization = container.get(CONTAINER_AUTHORIZATION);

    const { pathname, host, searchParams, tld } = useUrl();

    authorization.setRedirectUrl({
        pathname,
        searchParams,
        host: typeof window === 'undefined' ? serverSideHost : host,
        tld: typeof window === 'undefined' ? getTldFromHost(serverSideHost) : tld
    });

    authorization.setTokenOwnerLogin?.(user.account?.login);
    authorization.check();

    return children;
});
