import { Platform } from '../types/Platform.js';

import { ClientPlatform } from './types/ClientPlatform.js';

export const getPlatformName = (platform: Platform | undefined): string | null => {
    switch (platform) {
        case 'win32':
            return ClientPlatform.WINDOWS;
        case 'darwin':
            return ClientPlatform.MACOS;
        case 'linux':
            return ClientPlatform.LINUX;
    }

    return null;
};
