import { cast, flow, getEnv, Instance, types, getRoot } from 'mobx-state-tree';

import { HttpErrorException, HttpResponseStatusCode } from '@yandex-music-int/http-client';
import {
    Album as AlbumEntity,
    AlbumsResourceWithTracksIdsResponse,
    AlbumsResourceWithTracksParams,
    AlbumType,
    ArtistsResourceSafeDirectAlbumsParams,
    ArtistsResourceSafeDirectAlbumsResponse,
    Period,
    TopResourceCategoryParams,
    TopResourceCategoryResponse,
    TopResourceCategoryType,
    Track,
    TracksMetaParams
} from '@yandex-music-int/resource-collection';

import { AlbumWithArtists, albumWithArtistsResponseToModel } from '@/features/album/index.js';

import { LoadingState, StoreEnv, Experiments } from '@/shared/lib/index.js';

import { getPreparedIds } from '../lib/getPreparedIds.js';
import { insertDataToVolumeItems } from '../lib/insertDataToVolumeItems.js';
import { makeFlatVolumeItems } from '../lib/makeFlatVolumeItems.js';
import { VolumeType } from '../lib/types/VolumeType.js';
import { updateVolumeItemsState } from '../lib/updateVolumeItemsState.js';

import { RelatedAlbums } from './RelatedAlbums.js';
import { VolumeItem } from './VolumeItem.js';

const INITIAL_TRACKS_COUNT_TO_FETCH = 10;
const RELATED_ALBUMS_PAGE_SIZE = 8;

interface ArtistsResourceParams extends Omit<ArtistsResourceSafeDirectAlbumsParams, 'artistId'> {
    artistId?: string;
}

export const Album = types
    .model('Album', {
        meta: types.maybeNull(AlbumWithArtists),
        items: types.array(VolumeItem),
        indexItems: types.map(types.number),
        trackIndexInContext: types.map(types.number),
        loadingState: types.enumeration(Object.values(LoadingState)),
        errorStatusCode: types.maybeNull(types.number),
        latestGenreAlbums: RelatedAlbums,
        otherArtistAlbums: RelatedAlbums,
        otherAlbumVersions: types.array(AlbumWithArtists)
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => {
        return {
            get isNonMusicType(): boolean {
                if (self.meta?.type) {
                    return [AlbumType.PODCAST, AlbumType.AUDIOBOOK].includes(self.meta.type);
                }

                return false;
            }
        };
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => {
        return {
            get isOtherArtistAlbumsAvailable(): boolean {
                return Boolean(
                    !self.isNonMusicType && self.meta?.artists?.length === 1 && !self.meta?.artists[0]?.various
                );
            },

            get isLatestGenreAlbumsAvailable(): boolean {
                return Boolean(!self.isNonMusicType && self.meta?.genre);
            },

            get isNotFound(): boolean {
                return (
                    self.loadingState === LoadingState.REJECT &&
                    self.errorStatusCode === HttpResponseStatusCode.NOT_FOUND
                );
            },

            get isAlbumLoadingError(): boolean {
                return self.loadingState === LoadingState.REJECT;
            },

            get isOtherAlbumVersionsExist(): boolean {
                return Boolean(
                    self.loadingState === LoadingState.IDLE ||
                        self.loadingState === LoadingState.PENDING ||
                        (self.otherAlbumVersions && self.otherAlbumVersions.length > 0)
                );
            },

            get isLatestGenreAlbumsExist(): boolean {
                return Boolean(
                    self.latestGenreAlbums.loadingState === LoadingState.IDLE ||
                        self.latestGenreAlbums.loadingState === LoadingState.PENDING ||
                        (self.latestGenreAlbums.items && self.latestGenreAlbums.items.length > 0)
                );
            },

            get isOtherArtistAlbumsExist(): boolean {
                return Boolean(
                    self.otherArtistAlbums.loadingState === LoadingState.IDLE ||
                        self.otherArtistAlbums.loadingState === LoadingState.PENDING ||
                        (self.otherArtistAlbums.items && self.otherArtistAlbums.items.length > 0)
                );
            },

            get isAlbumLoading(): boolean {
                return self.loadingState === LoadingState.IDLE || self.loadingState === LoadingState.PENDING;
            },

            get isLatestGenreAlbumsLoading(): boolean {
                return (
                    self.latestGenreAlbums.loadingState === LoadingState.IDLE ||
                    self.latestGenreAlbums.loadingState === LoadingState.PENDING
                );
            },

            get isOtherArtistAlbumsLoading(): boolean {
                return (
                    self.otherArtistAlbums.loadingState === LoadingState.IDLE ||
                    self.otherArtistAlbums.loadingState === LoadingState.PENDING
                );
            }
        };
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getLatestGenreAlbums: flow(function* (
                params: TopResourceCategoryParams
            ): Generator<Promise<TopResourceCategoryResponse>, void, TopResourceCategoryResponse> {
                const { topResource, logger } = getEnv<StoreEnv>(self);

                if (self.latestGenreAlbums.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.latestGenreAlbums.loadingState = LoadingState.PENDING;

                    const data = yield topResource.getTopByGenre(params);

                    self.latestGenreAlbums.items = cast(data.albums.map(albumWithArtistsResponseToModel));

                    self.latestGenreAlbums.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    self.latestGenreAlbums.loadingState = LoadingState.REJECT;
                }
            })
        };
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getOtherArtistAlbums: flow(function* (
                params: ArtistsResourceParams,
                albumId: number
            ): Generator<
                Promise<ArtistsResourceSafeDirectAlbumsResponse>,
                void,
                ArtistsResourceSafeDirectAlbumsResponse
            > {
                const { artistsResource, logger } = getEnv<StoreEnv>(self);

                if (self.otherArtistAlbums.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    if (!params.artistId) {
                        self.otherArtistAlbums.loadingState = LoadingState.REJECT;

                        return;
                    }

                    self.otherArtistAlbums.loadingState = LoadingState.PENDING;

                    const data = yield artistsResource.getSafeDirectAlbums({
                        ...params,
                        artistId: params.artistId
                    });

                    self.otherArtistAlbums.items = cast(
                        data.albums
                            .filter((album: AlbumEntity) => String(album.id) !== String(albumId))
                            .map(albumWithArtistsResponseToModel)
                    );

                    self.otherArtistAlbums.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    self.otherArtistAlbums.loadingState = LoadingState.REJECT;
                }
            })
        };
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getTracks: flow(function* ({ trackIds }: TracksMetaParams): Generator<Promise<Track[]>, void, Track[]> {
                const { tracksResource, logger } = getEnv<StoreEnv>(self);

                try {
                    if (!self.meta?.id) {
                        return;
                    }

                    updateVolumeItemsState(trackIds, self.items, self.indexItems, LoadingState.PENDING);

                    const data = yield tracksResource.getTracksMeta({
                        trackIds: getPreparedIds(trackIds, self.meta.id)
                    });

                    insertDataToVolumeItems(data, self.items, self.indexItems);
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    updateVolumeItemsState(trackIds, self.items, self.indexItems, LoadingState.REJECT);
                }
            })
        };
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getData: flow(function* ({
                albumId
            }: AlbumsResourceWithTracksParams): Generator<
                Promise<AlbumsResourceWithTracksIdsResponse | unknown>,
                void,
                AlbumsResourceWithTracksIdsResponse
            > {
                const { albumResource, logger } = getEnv<StoreEnv>(self);
                // @ts-ignore
                const { experiments } = getRoot(self);
                const isAlbumVersionsDisabled = experiments.checkExperiment(
                    Experiments.WebNextAlbumDisableVersions,
                    'on'
                );
                const isAlbumTracksDisabled = experiments.checkExperiment(Experiments.WebNextAlbumDisableTracks, 'on');
                const isLatestGenreAlbumsDisabled = experiments.checkExperiment(
                    Experiments.WebNextAlbumDisableLatestGenreAlbums,
                    'on'
                );
                const isOtherArtistAlbumsDisabled = experiments.checkExperiment(
                    Experiments.WebNextAlbumDisableOtherArtistAlbums,
                    'on'
                );

                if (self.loadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const data = yield albumResource.getAlbumWithTracksIds({
                        albumId,
                        resumeStream: false
                    });

                    if (data?.error === 'not-found') {
                        self.errorStatusCode = HttpResponseStatusCode.NOT_FOUND;
                        self.loadingState = LoadingState.REJECT;
                        self.otherArtistAlbums.loadingState = LoadingState.REJECT;
                        self.latestGenreAlbums.loadingState = LoadingState.REJECT;
                        return;
                    }

                    self.meta = albumWithArtistsResponseToModel(data);

                    if (!isAlbumVersionsDisabled && data.duplicates && data.duplicates.length > 0) {
                        self.otherAlbumVersions = cast(data.duplicates?.map(albumWithArtistsResponseToModel));
                    }

                    if (!isAlbumTracksDisabled) {
                        makeFlatVolumeItems(data, self.items, self.indexItems, self.trackIndexInContext);

                        const initialTrackIds: string[] = [];

                        for (let index = 0; index < INITIAL_TRACKS_COUNT_TO_FETCH; index++) {
                            const item = self.items[index];
                            if (item?.type === VolumeType.TRACK) {
                                initialTrackIds.push(String(item.id));
                            }
                        }

                        yield self.getTracks({ trackIds: initialTrackIds });
                    }
                    let latestGenreAlbumsRequest;
                    let getOtherArtistAlbumsRequest;

                    if (!isLatestGenreAlbumsDisabled && self.isLatestGenreAlbumsAvailable) {
                        latestGenreAlbumsRequest = self.getLatestGenreAlbums({
                            category: TopResourceCategoryType.RECENT_ALBUMS,
                            period: Period.WEEK,
                            pageSize: RELATED_ALBUMS_PAGE_SIZE,
                            genre: data.genre
                        });
                    } else {
                        self.latestGenreAlbums.loadingState = LoadingState.RESOLVE;
                    }

                    if (!isOtherArtistAlbumsDisabled && self.isOtherArtistAlbumsAvailable) {
                        getOtherArtistAlbumsRequest = self.getOtherArtistAlbums(
                            {
                                artistId: String(data.artists[0]?.id),
                                limit: RELATED_ALBUMS_PAGE_SIZE
                            },
                            albumId
                        );
                    } else {
                        self.otherArtistAlbums.loadingState = LoadingState.RESOLVE;
                    }

                    yield Promise.allSettled([latestGenreAlbumsRequest, getOtherArtistAlbumsRequest]);

                    // @ts-expect-error
                    // TS ,     ,         ,
                    //      / ,     IDLE
                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.RESOLVE;
                    }
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }

                    if (
                        error instanceof HttpErrorException &&
                        (error.statusCode === HttpResponseStatusCode.NOT_FOUND ||
                            error.statusCode === HttpResponseStatusCode.BAD_REQUEST)
                    ) {
                        self.errorStatusCode = HttpResponseStatusCode.NOT_FOUND;
                    }

                    if (self.loadingState !== LoadingState.IDLE) {
                        self.loadingState = LoadingState.REJECT;
                        self.otherArtistAlbums.loadingState = LoadingState.REJECT;
                        self.latestGenreAlbums.loadingState = LoadingState.REJECT;
                    }
                }
            }),
            reset() {
                self.meta = null;
                self.errorStatusCode = null;
                self.items = cast([]);
                self.indexItems.clear();
                self.trackIndexInContext.clear();
                self.loadingState = LoadingState.IDLE;
                self.latestGenreAlbums.loadingState = LoadingState.IDLE;
                self.latestGenreAlbums.items = cast([]);
                self.otherArtistAlbums.loadingState = LoadingState.IDLE;
                self.otherArtistAlbums.items = cast([]);
                self.otherAlbumVersions = cast([]);
            }
        };
    });

export type AlbumModel = Instance<typeof Album>;
