import { types, Instance, flow, getEnv, cast } from 'mobx-state-tree';

import {
    UsersResourceLikedPlaylists,
    UsersResourceLikedPlaylistsParams,
    Playlist as PlaylistApi
} from '@yandex-music-int/resource-collection';

import { Playlist, playlistResponseToModel } from '@/entities/playlist/index.js';

import { LoadingState, StoreEnv } from '@/shared/lib/index.js';
import { Sort, Pager } from '@/shared/models/index.js';

import { updateItemsByPage } from '../lib/updateItemsByPage.js';

export const PlaylistsLiked = types
    .model('PlaylistsLiked', {
        loadingState: types.enumeration(Object.values(LoadingState)),
        sort: types.maybeNull(Sort),
        items: types.array(types.maybeNull(Playlist)),
        pager: types.maybeNull(Pager),
        alreadyRequestedPages: types.map(types.number),
        requests: types.maybeNull(types.number)
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getData: flow(function* ({
                userId,
                page = 0,
                pageSize = 20,
                sortBy,
                sortOrder,
                metaType,
                withTracks
            }: UsersResourceLikedPlaylistsParams): Generator<
                Promise<UsersResourceLikedPlaylists>,
                void,
                UsersResourceLikedPlaylists
            > {
                if (self.alreadyRequestedPages.has(`${page}`)) {
                    return;
                }

                if (typeof page === 'number') {
                    self.alreadyRequestedPages.set(`${page}`, page);
                }

                const { usersResource, logger } = getEnv<StoreEnv>(self);

                self.sort = null;

                if (sortBy && sortOrder) {
                    self.sort = {
                        sortBy,
                        sortOrder
                    };
                }

                try {
                    self.loadingState = LoadingState.PENDING;

                    const { likedPlaylists, pager } = yield usersResource.getLikedPlaylists({
                        userId,
                        page,
                        pageSize,
                        sortBy,
                        sortOrder,
                        metaType,
                        withTracks
                    });

                    self.requests = (self.requests ?? 0) + 1;

                    if (self.items.length === 0) {
                        self.items = cast(Array.from({ length: pager.total }, () => null));
                    }

                    const mappedRawItems = likedPlaylists.map(({ playlist }: { playlist: PlaylistApi }) =>
                        playlistResponseToModel(playlist)
                    );

                    updateItemsByPage({
                        items: self.items,
                        mappedRawItems,
                        page,
                        pageSize
                    });

                    self.pager = pager;

                    self.loadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (typeof error === 'string' || error instanceof Error) {
                        logger.error(error);
                    }
                    if (typeof page === 'number') {
                        self.alreadyRequestedPages.delete(`${page}`);
                    }
                    self.requests = typeof self.requests === 'number' ? self.requests - 1 : 0;
                    self.loadingState = LoadingState.REJECT;
                }
            })
        };
    });

export type PlaylistsLikedModel = Instance<typeof PlaylistsLiked>;
