import { types, Instance } from 'mobx-state-tree';

import { TrackWithArtists } from '@/features/track/index.js';

import { LoadingState } from '@/shared/lib/index.js';

import { VolumeType } from '../lib/types/VolumeType.js';

export const VolumeItemTrack = types.model('VolumeItemTrack', {
    type: types.literal(VolumeType.TRACK),
    id: types.union(types.string, types.number),
    data: types.maybeNull(TrackWithArtists),
    position: types.maybe(types.number),
    loadingState: types.enumeration(Object.values(LoadingState))
});

export const VolumeItemText = types.model('VolumeItemText', {
    type: types.literal(VolumeType.TEXT),
    data: types.maybeNull(types.number)
});

export const VolumeItem = types.union(VolumeItemText, VolumeItemTrack);

export type VolumeItemModel = Instance<typeof VolumeItem>;
export type VolumeItemTextModel = Instance<typeof VolumeItemText>;
export type VolumeItemTrackModel = Instance<typeof VolumeItemTrack>;
