'use client';

import { ReactElement, useMemo } from 'react';

import {
    YaMusicAnalytics,
    createEventTracker,
    createGlobalParamsProvider,
    createPlatformParamsProvider
} from '@yandex-music-int/analytics';

import { AnalyticsContext, WithChildren, useLogger, useMetrika } from '@/shared/lib/index.js';

interface Props extends WithChildren {}

let singletonAnalytics: YaMusicAnalytics | null = null;

export const AnalyticsProvider = ({ children }: Props): ReactElement => {
    const metrika = useMetrika();
    const logger = useLogger();
    const analytics = useMemo(() => {
        if (typeof window === 'undefined' || !metrika.isLoaded) {
            return null;
        }

        if (singletonAnalytics) {
            return singletonAnalytics;
        }

        const eventTracker = createEventTracker(
            (events: Array<Record<string, unknown>>) => {
                return metrika.count(events, 'evgen');
            },
            logger,
            false
        );

        const globalParamsProvider = createGlobalParamsProvider();
        const platformParamsProvider = createPlatformParamsProvider();

        singletonAnalytics = new YaMusicAnalytics(eventTracker, globalParamsProvider, platformParamsProvider);

        return singletonAnalytics;
    }, [metrika, logger]);

    return <AnalyticsContext.Provider value={analytics}>{children}</AnalyticsContext.Provider>;
};
