'use client';

import { observer } from 'mobx-react-lite';
import { ReactNode, use } from 'react';

import { useStore, WithChildren, LoadingState, useContainer, CONTAINER_AUTHORIZATION } from '@/shared/lib/index.js';

import { setClientSideLayout } from './LayoutProvider.js';

interface Props extends WithChildren {}

const prefetchRequests = async (store: RootModel, hasAuthorizationCredentials: boolean): Promise<void> => {
    const { user, library, experiments, slides } = store;

    //           ,   100%  
    //        (account/about),      
    const authorizationAction = hasAuthorizationCredentials ? user.getAbout : user.setUnauthorized;

    await Promise.all([authorizationAction(), experiments.getData()]);

    if (user.isAuthorized) {
        await Promise.all([user.getSettings(), library.getData(), slides.getUserSlides()]);
    }
};

export const PrefetchDataProvider = observer(({ children }: Props): ReactNode => {
    const store = useStore();
    const container = useContainer();
    const authorization = container.get(CONTAINER_AUTHORIZATION);
    const hasAuthorizationCredentials = authorization.hasAuthorizationCredentials;

    /*
        ..  settings   ,     
            ,     
    */
    setClientSideLayout(store.settings);

    if (store.user.account.loadingState === LoadingState.IDLE) {
        use(prefetchRequests(store, hasAuthorizationCredentials));
    }

    return children;
});
