import { SearchItem as SearchItemResponse, SearchType } from '@yandex-music-int/resource-collection';

import { SearchItemModel } from '@/widgets/search/model/SearchItem.js';

import { albumWithArtistsResponseToModel } from '@/features/album/index.js';
import { playlistWithArtistsResponseToModel } from '@/features/playlist/index.js';
import { trackWithArtistsResponseToModel, ugcTrackWithArtistsResponseToModel } from '@/features/track/index.js';

import { artistResponseToModel } from '@/entities/artist/index.js';

export const searchInstantMixedResultsResponseToModel = (results: SearchItemResponse[]): SearchItemModel[] => {
    return results.map((item: SearchItemResponse): SearchItemModel => {
        switch (item.type) {
            case SearchType.ALBUM:
                return {
                    type: SearchType.ALBUM,
                    data: albumWithArtistsResponseToModel(item.album)
                };
            case SearchType.ARTIST:
                return {
                    type: SearchType.ARTIST,
                    data: artistResponseToModel(item.artist)
                };
            case SearchType.PLAYLIST:
                return {
                    type: SearchType.PLAYLIST,
                    data: playlistWithArtistsResponseToModel({
                        ...item.playlist,
                        artists: []
                    })
                };
            case SearchType.UGC_TRACK:
                return {
                    type: SearchType.UGC_TRACK,
                    data: ugcTrackWithArtistsResponseToModel(item.track)
                };
            case SearchType.TRACK:
                return {
                    type: SearchType.TRACK,
                    data: trackWithArtistsResponseToModel(item.track)
                };
        }
    });
};
