'use client';

import { enableStaticRendering } from 'mobx-react-lite';
import { onSnapshot, applySnapshot, SnapshotIn } from 'mobx-state-tree';
import { ServerInsertedHTMLHook } from 'next/dist/shared/lib/server-inserted-html.js';
// @ts-expect-error        next,    es-modules
import { ServerInsertedHTMLContext } from 'next/navigation';
import { ReactElement, useRef, useContext, useState, useLayoutEffect } from 'react';

import { WithChildren, StoreContext, useResources, useLogger } from '@/shared/lib/index.js';

import { defaultState } from '../store/defaultState.js';
import { createRootModel, excludeModelsFromRehydration } from '../store/index.js';

//   process.browser?
enableStaticRendering(typeof window === 'undefined');

interface Props extends WithChildren {
    nonce?: string;
}

export const StoreProvider = ({ children, nonce }: Props): ReactElement => {
    const rehydrationContextSnapshot = useRef<Array<SnapshotIn<RootModel>>>([]);
    const insertHtml = useContext<ServerInsertedHTMLHook>(ServerInsertedHTMLContext);
    const resources = useResources();
    const logger = useLogger();
    // eslint-disable-next-line react/hook-use-state
    const [store] = useState(() => {
        const storeEnv = {
            ...resources,
            logger
        };

        if (typeof window !== 'undefined' && window.__STATE_SNAPSHOT__?.length) {
            return createRootModel(window.__STATE_SNAPSHOT__[window.__STATE_SNAPSHOT__.length - 1], storeEnv);
        }
        return createRootModel(defaultState, storeEnv);
    });

    if (typeof window === 'undefined') {
        if (insertHtml) {
            insertHtml(() => {
                return (
                    <script
                        dangerouslySetInnerHTML={{
                            //   ,
                            //     ( Next   -  ,        ),
                            //    ,      store
                            __html: `var musicStateSnapshotUpdatingEvent = new Event('yMusicStateSnapshotUpdated');`
                        }}
                        nonce={nonce}
                    />
                );
            });
        }

        onSnapshot(store, (newSnapshot: SnapshotIn<RootModel>) => {
            rehydrationContextSnapshot.current.push(newSnapshot);

            if (insertHtml && rehydrationContextSnapshot.current) {
                insertHtml(() => {
                    const snapshots = rehydrationContextSnapshot.current;

                    rehydrationContextSnapshot.current = [];

                    if (snapshots.length === 0) {
                        return;
                    }

                    return (
                        <script
                            dangerouslySetInnerHTML={{
                                __html: `(window.__STATE_SNAPSHOT__ = window.__STATE_SNAPSHOT__ || []).push(${JSON.stringify(
                                    snapshots[snapshots.length - 1]
                                ).replace(/</g, '\\u003c')}); window.dispatchEvent(musicStateSnapshotUpdatingEvent);`
                            }}
                            nonce={nonce}
                        />
                    );
                });
            }
        });
    }

    useLayoutEffect(() => {
        function syncStores(): void {
            const lastSnapshot = window.__STATE_SNAPSHOT__?.[window.__STATE_SNAPSHOT__.length - 1];

            if (lastSnapshot) {
                applySnapshot(store, excludeModelsFromRehydration(store, lastSnapshot));
            }

            delete window.__STATE_SNAPSHOT__;
        }

        syncStores();

        window.addEventListener('yMusicStateSnapshotUpdated', syncStores);

        return () => window.removeEventListener('yMusicStateSnapshotUpdated', syncStores);
    }, [store]);

    return <StoreContext.Provider value={store}>{children}</StoreContext.Provider>;
};
